textarea{
    font-family: inherit;
    font-size: 100%;
}

form{
    overflow: auto;
}

textarea{
    width: 100%;
    outline: none;
    border: none;
    background-color: #f9f9f9;;
    border-bottom: 1px solid lightgray;
    padding: 12px 20px 12px 20px;
    border-color: transparent;
}

.commentBox{
    position: relative;
}

.commentLine{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: black;
    transform: scaleX(0);
    animation: commentLineScaleIn 0.2s forwards;
}

@keyframes commentLineScaleIn{
    0%{
        transform: scaleX(0);
    }
    100%{
        transform: scale(1);
    }
}

.commentButton{
    font-family: inherit;
    font-size: 0.9rem;
    font-weight: bold;
    float: right;
    border: none;
    margin: 10px 0 0 10px;
    padding: 10px 20px 10px 20px;
    cursor: pointer;
    border-radius: 2px;
}

.sendButton{
    background-color: #065fd4;
    color: white;
}

.sendButton:disabled{
    color:white;
    cursor: default;
    background-color: #cccccc;
}